<template>
  <div class="d-flex align-items-center min-vh-100">
    <CContainer fluid>
      <CRow class="justify-content-center">
        <CCol md="6">
          <CCard class="mx-4 mb-0">
            <CCardBody class="p-4">
              <CForm validated  @submit="register">
                <h1>Register</h1>
                <p class="text-muted">Create your account</p>
                <CInput 
                required
                  placeholder="name"
                  autocomplete="name"
                  v-model="name"
                >
                  <template #prepend-content><CIcon name="cil-user"/></template>
                </CInput>
                <CInput
                  placeholder="Email"
                  autocomplete="email"
                  prepend="@"
                  v-model="email"
                  type="email"
                  required
                />
                <CInput
                  placeholder="Password"
                  type="password"
                  autocomplete="new-password"
                  v-model="password"
                   required
                >
                  <template #prepend-content><CIcon name="cil-lock-locked"/></template>
                </CInput>
        
                <CButton  type="submit" color="success" block>Create Account</CButton>
              </CForm>
            </CCardBody>
          </CCard>
        </CCol>
      </CRow>
    </CContainer>
  </div>
</template>

<script>
export default {
  name: 'Register',
   data() {
        return {
          name:'',
          email: '',
          password: '',
        }
    },
    methods: {
register(){
  let loader = this.$loading.show();
   this.axios.post("users", {"name":this.name,"email":this.email,"hashedpassword":this.password})
            .then(response => {
              loader.hide();
              this.$toastr.s("SUCCESS MESSAGE", "Success Toast Title");
              this.$router.push('/login')

            }).catch(error=>{
               loader.hide();
               this.$toastr.e("error MESSAGE", "error Toast Title");
            });
}
  }
}
</script>
